body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/*Start general*/
.hide {
    display: none;
}
/*End general*/

/*Start Sidebar*/

.sidebar ul {
    list-style-type: none;
}

.sidebar input {
    display: none;
}

.sidebar input.block {
    display: block;
}

/*End Sidebar*/


/* Start StudyModal*/
.study-modal {
    background: pink;
}
/* End  StudyModal*/



